<template>
  <div class="search-result"
  :class="{bottom:currentmusic.id?true:false}"
  >
    <best-match :bestmatch="bestmatch"></best-match>
    <music-item
      v-if="sreachresult.length > 0"
      :musicitem="sreachresult"
      @play="$emit('play', $event)"
      :currentmusic="currentmusic"
      :currentstate="currentstate"
    ></music-item>
    <load-animation v-else></load-animation>
    <div class="loadmore" v-if="sreachresult.length > 0" @click.stop="more">
      加载更多...
    </div>
  </div>
</template>

<script>
import BestMatch from './BestMatch.vue';
import LoadAnimation from "./LoadAnimation.vue";
import MusicItem from "./MusicItem.vue";
export default {
  components: { MusicItem, LoadAnimation ,BestMatch},
  props: {
    keywords: {
      type: String,
      default: "",
    },
    currentmusic: {
      type: Object,
      default: () => {
        return null;
      },
    },
    currentstate: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      sreachresult: [],
      bestmatch: {},
      pages: 1,
    };
  },
  created() {
    this.getSreachResult();
    this.getBestMatch();
  },
  methods: {
    async getSreachResult(offset = 0) {
      await this.axios
        .get(
          `/cloudsearch?keywords=${this.keywords}&type=1&limit=10&offset=${offset}`
        )
        .then((res) => {
          console.log("res=>", res);
          this.setHistroy();
          console.log(res);
          this.sreachresult = this.sreachresult.concat(
            res.result.songs.map((item) => {
              item.song = {
                album: item.al,
                alias: item.alia,
                artists: item.ar,
                privilege: item.privilege,
              };
              return item;
            })
          );
        });
    },
    more() {
      this.pages++;
      let offset = (this.pages - 1) * 10;
      this.getSreachResult(offset);
    },
    // // 最佳匹配
    getBestMatch(){
       this.axios.get(`/search/multimatch?keywords=${this.keywords}`).then((res)=>{
         console.log("最佳匹配=>",res.result);
         this.bestmatch = res.result;
         console.log(this.bestmatch);
       })
    },
    // 历史记录
    setHistroy() {
      if (!this.keywords || this.keywords == "" || this.keywords.length < 1) {
        return;
      }
      let histroys = localStorage.getItem("musichistroy");
      let histroyArr = [];
      if (histroys) {
        histroyArr = JSON.parse(histroys); //还原localstorage数据
      }
      histroyArr.unshift(this.keywords); //把最新的搜索词头部添加到存储localstorage
      histroyArr = [...new Set(histroyArr)]; //去重,数组
      // console.log(histroyArr);
      localStorage.setItem("musichistroy", JSON.stringify(histroyArr));
    },
  },
};
</script>

<style lang="scss" scoped>
.search-result {
  &.bottom{
padding-bottom: 60px;
  }
  .loadmore {
    text-align: center;
    font-size: 12px;
    color: gray;
    height: 40px;
    line-height: 40px;
  }
}
</style>