<template>
    <div class="search-hot">
        <h3>热门搜索</h3>
        <ul v-if="hotlist.length > 0">
            <li v-for="hot in hotlist" :key="hot.first" @click.stop="$emit('search', hot.first)">
                {{ hot.first }}
            </li>
        </ul>
        <load-animation v-else></load-animation>
        <div class="histroy">
            <h4>历史记录</h4>
            <span class="iconfont icon-lajitong" @click.stop="clearhhistroy"></span>
        </div>
        <ol>
            <li v-for="item in histroyArr" :key="item" @click.stop="$emit('search', item)">
                <span class="keywords">{{ item }}</span>
                <span class="iconfont icon-cuowu" @click.stop="delHistroy(item)"></span>
            </li>
        </ol>
    </div>
</template>

<script>
import LoadAnimation from "./LoadAnimation.vue";
export default {
    components: { LoadAnimation },
    data() {
        return {
            hotlist: [], //存储热门搜索列表
            histroyArr: [],
        };
    },
    created() {
        this.getSearchList();
        this.getHistroy();
    },
    methods: {
        getSearchList() {
            this.axios.get("/search/hot").then((res) => {
                this.hotlist = res.result.hots;
            });
        },
        getHistroy() {
            let histroy = localStorage.getItem("musichistroy");
            if (histroy) {
                this.histroyArr = JSON.parse(histroy);
            } else {
                this.histroyArr = [];
            }
        },
        delHistroy(event) {
            let histroyWords = localStorage.getItem("musichistroy");
            console.log(histroyWords);
            this.histroyArr = JSON.parse(histroyWords).filter((v) => {
                return v != event;
            });
            localStorage.setItem("musichistroy", JSON.stringify(this.histroyArr));
        },
        clearhhistroy() {
            this.histroyArr = [];
            localStorage.setItem("musichistroy", JSON.stringify(this.histroyArr));
        },
    },
};
</script>

<style lang="scss" scoped>
.search-hot {
    h3 {
        margin: 15px 0px 5px 15px;
        font-size: 13px;
        color: rgb(102, 102, 102);
    }
    .histroy {
        display: flex;
        align-items: center;
        justify-content: space-between;
        h4 {
            margin: 15px 0px 5px 15px;
            font-size: 13px;
            color: rgb(102, 102, 102);
        }
        .icon-lajitong {
            font-size: 18px;
            margin-right: 22px;
        }
    }

    ul {
        padding-left: 10px;
        display: flex;
        flex-wrap: wrap;
        li {
            padding: 6px 10px;
            margin: 6px;
            border: 1px solid rgba(128, 128, 128, 0.297);
            border-radius: 20px;
            font-size: 12px;
        }
    }
    ol {
        padding: 0px 16px;
        li {
            height: 40px;
            line-height: 40px;
            border-bottom: 1px solid rgba(128, 128, 128, 0.093);
            position: relative;
            .keywords {
                font-size: 14px;
                color: rgb(130, 127, 127);
                padding-left: 20px;
                position: relative;
                &::before {
                    content: "";
                    display: block;
                    width: 16px;
                    height: 16px;
                    position: absolute;
                    left: 0;
                    top: 1px;
                    background-image: url(data:image/svg+xml;base64,);
                    background-repeat: no-repeat;
                    background-size: 16px 16px;
                    background-position: 0px 0px;
                }
            }
            .icon-cuowu {
                position: absolute;
                top: 1px;
                right: 10px;
                font-size: 16px;
            }
        }
    }
}
</style>
