<template>
  <div class="search-suggest">
    <h3>搜索:"{{ keywords }}"</h3>
    <ul>
      <li
        v-for="(s, i) in searchsuggest"
        :key="i"
        @click.stop="$emit('search', s.keyword)"
      >
        <span class="iconfont icon-chazhao"></span>
        <span class="keyword">{{ s.keyword }}</span>
      </li>
    </ul>
  </div>
</template>

<script>
import _ from "lodash";
export default {
  props: {
    keywords: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      searchsuggest: [], //存储热门搜索建议列表
    };
  },
  created() {
    this.getSearchSuggest();
  },
  methods: {
    async getSearchSuggest() {
      await this.axios
        .get(`/search/suggest?keywords=${this.keywords}&type=mobile`)
        .then((res) => {
          this.searchsuggest = res.result.allMatch;
        });
    },
  },
  // 侦听关键词变化的时候，用防抖
  watch: {
    keywords: _.debounce(function() {
      this.getSearchSuggest();
    }, 100),
  },
};
</script>

<style lang="scss" scoped>
.search-suggest {
  h3 {
    width: 360px;
    height: 40px;
    line-height: 40px;
    padding-left: 15px;
    font-size: 15px;
    color: rgba(47, 183, 237, 0.83);
    letter-spacing: 2px;
    border-bottom: 1px solid rgba(128, 128, 128, 0.048);
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    overflow: hidden;
  }
  ul {
    padding-left: 16px;
    li {
      width: 320px;
      height: 40px;
      line-height: 40px;
      border-bottom: 1px solid rgba(128, 128, 128, 0.075);
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 1;
      overflow: hidden;
      span {
        font-size: 16px;
        &.icon-chazhao {
          color: rgba(130, 127, 127, 0.693);
        }
        &.keyword {
          margin-left: 6px;
          font-size: 14px;
          color: rgba(0, 0, 0, 0.688);
        }
      }
    }
  }
}
</style>