<template>
    <div class="search">
        <div class="search-input">
            <input type="text" v-model="keywords" placeholder="搜索歌曲、歌手、专辑" @input.stop="inputing" @keydown.enter="entering" />
            <span class="iconfont icon-chazhao"></span>
            <span class="iconfont icon-cuowu" v-show="keywords.length > 0" @click.stop="clear"></span>
        </div>
        <component :is="componentName" :keywords="keywords" @search="search" @play="$emit('play', $event)" :currentmusic="currentmusic" :currentstate="currentstate"></component>
    </div>
</template>

<script>
import SearchHot from "@/components/SearchHot.vue";
import SearchSuggest from "@/components/SearchSuggest.vue";
import SearchResult from "@/components/SearchResult.vue";
export default {
    props: {
        currentmusic: {
            type: Object,
            default: () => {
                return null;
            },
        },
        currentstate: {
            type: Boolean,
            default: true,
        },
    },
    data() {
        return {
            keywords: "",
            componentName: "SearchHot",
        };
    },
    components: {
        SearchHot,
        SearchSuggest,
        SearchResult,
    },
    methods: {
        inputing() {
            if (this.keywords != "" || this.keywords.length > 0) {
                this.componentName = "SearchSuggest";
            } else {
                this.componentName = "SearchHot";
            }
        },
        entering() {
            this.componentName = "SearchResult";
            console.log("this.componentName==>", this.componentName);
        },
        // 点击热门搜索
        search(event) {
            this.componentName = "SearchResult";
            this.keywords = event;
        },
        clear() {
            this.keywords = "";
            this.componentName = "SearchHot";
        },
    },
};
</script>

<style lang="scss" scoped>
.search {
    .search-input {
        position: relative;
        border-bottom: 1px solid rgba(128, 128, 128, 0.171);
        input {
            width: 95%;
            height: 25px;
            border-radius: 15px;
            border: 0px;
            outline: 1px solid rgba(128, 128, 128, 0.325);
            margin: 4% 2%;
            background-color: rgb(230, 230, 232);
            color: gray;
            font-size: 14px;
            padding-left: 24px;
            box-sizing: border-box;
        }
        .icon-chazhao {
            position: absolute;
            top: 22px;
            left: 13px;
            font-size: 15px;
            color: gray;
        }
        .icon-cuowu {
            position: absolute;
            top: 22px;
            right: 22px;
            font-size: 12px;
        }
    }
}
input::-webkit-input-placeholder {
    //webkit内核浏览器
    color: rgba(128, 128, 128, 0.488);
    font-size: 14px;
}
:-moz-placeholder {
    //火狐浏览器 18-
    color: rgba(128, 128, 128, 0.488);
    font-size: 14px;
}
::moz-placeholder {
    //火狐浏览器 19+
    color: rgba(128, 128, 128, 0.488);
    font-size: 14px;
}
:-ms-input-placeholder {
    //IE浏览器
    color: rgba(128, 128, 128, 0.488);
    font-size: 14px;
}
</style>
